@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {

  box-sizing: border-box;
  margin: 0;
  padding: 0;

}

a {
  text-decoration:none;
  color:lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothig: antialiased;
  -moz-osx-font-smoothing:grayscale;
  background-color: #282c34;
  color:white;
  max-width: 1100px;
  margin: 0 auto;
  /* background-image: url('./assets/Kolkata-Knight-Riders-Logo_freelogovectors.net_.png'); */
}

.App{text-align:left;padding:20px}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}.MatchSmallCard .additional-details{text-align:right}.MatchSmallCard .additional-details>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:10px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .win-loss-section{-webkit-align-self:center;align-self:center;padding:30px;text-align:center}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}.TeamPage .logo{width:20px;height:20px}.TeamPage .icon{width:150px;height:150px}.TeamPage .img{transition:1s}.TeamPage .img:hover{-webkit-transform:rotate(360);transform:rotate(360)}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr}.MatchPage .year-selector{justify-self:end;padding:20px}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px}.TeamTile .icon{width:180px;height:100px;margin:auto}.TeamTile .kkr{width:100px;height:100px}.TeamTile .mi{width:100px;height:100px}

.HomePage .app-name{font-size:3rem;text-align:center;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}

